* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    width: 800px;
}

.heading {
    align-items: center;
    text-align: center;
}

.image {
    width: 300px;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid yellowgreen;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.posts-not-found {
    align-items: center;
    text-align: center;
}

.post__buttons {
    display: flex;
}

.page__wrapper {
    margin-top: 30px;
    display: flex;
}

.page {
    padding: 10px;

    margin: 0 5px;
    border: 1px solid yellowgreen;
    cursor: pointer;
}

.page__current {
    border: 2px solid blue;
    font-weight: bold;
}

.navbar {
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: lightcoral;
}

.navbar__links {
    margin-left: auto;
    margin-right: 0;
}

.categories__list {
    display: flex;
}

.categories__image {
    height: 100px;
    width: 100px;
}